.feed{
    grid-area: feed;
    width:100%;
    max-width: 140rem;
    margin: 0 auto;
    display: grid;
    grid-template-columns:26rem 1fr 25rem;
    column-gap: 4rem;
    row-gap: 2rem;
    grid-template-rows: 4rem 1fr;
    grid-template-areas:
    ". . user"
    "side work-space user";
}